.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  color: white;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(79deg,#7439db,#C66FBC 50%,#F7944D)

}


.auth-form-container,.login-form,.register-form
{
  display: flex;
  flex-direction: column;
  
}
label{
  text-align: left;
  padding: 0.25rem 0;
}
input{
  margin: 0.5rem 0;
  padding: 1rem;
  border: none;
  border-radius: 10px;
}
button{
  border: none;
  background-color: white;
  color:#7439db;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
}
.link-btn
{
  background: none;
  color: white;
  text-decoration: underline;
}